<template>
	<view class="container">
		<view>
			<swiper indicator-dots autoplay circular :interval="1500">
				<swiper-item v-for="(item,index) in bundleList" :key="item.goods_id">
					<image :src="item.image_src"></image>
				</swiper-item>
			</swiper>
		</view>
		<view class="tarbar">
			
		</view>
	</view>
</template>

<script>
	import {
		getBundleList
	} from '@/api/home/index.js'
	export default {
		name: 'HomeHeader',
		data() {
			return {
				bundleList: []
			}
		},
		mounted() {
			this.getBundleList()
		},
		methods: {
			getBundleList() {
				getBundleList().then(result => {
					this.bundleList = result.message
				})
			}
		}
	}
</script>

<style lang="scss">
	.container {
		swiper {
			width: 750rpx;
			height: 380rpx;
			image {
				height: 100%;
				width: 100%;
			}
		}
	}
</style>
